body {
    position: absolute;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    height: 100%;
    width: 100%;
    background-color: #e0e0d1;
}


.sidenav {
	height: 100%;
	width: 250px;
	position: fixed;
	z-index: 1;
	top: 0;
	left: 0;
	overflow-x: hidden;
	background-color: #111;
	color: #818181;
	text-align: center;
	font-size: 20px;
	padding-top: 20px;
	opacity: 0;
	-webkit-animation-name: fadein;
	        animation-name: fadein;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes fadein {
	0% {opacity: 0;}
	100% {opacity: 1;}
}

@keyframes fadein {
	0% {opacity: 0;}
	100% {opacity: 1;}
} 


.sidenav img {
	border: solid;
	border-color: #296781;
	height: auto;
	width: 175px;
	border-width: 5px;
	margin-top: 15px;
	margin-bottom: 60px;
}
.sidenav div {
	height: 50px;
	width: 80%;
	display: table;
	margin: auto;
}

.sidenav .element:hover {
  color: #f1f1f1;
  background-color: #2b2222;
  cursor: pointer;
}

.element {
	display: table-cell;
	vertical-align: middle;
}

.elementselected {
	display: table-cell;
	vertical-align: middle;
	background-color: #296781;
	padding: 0;
	margin: 0;
	color: #f1f1f1;
	cursor: pointer;
}

.aboutpage {
	text-align: center;
	-webkit-animation-name: fadein;
	        animation-name: fadein;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.aboutpage img {
